.home-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  color: #fff;
  &.green {
    background-color: #00b4aa;
  }
  &.grey {
    background-color: #615f5f;
  }
  &.is-phone {
    .icon-menu {
      right: 8vw;
      bottom: 8vw;
      font-size: 6vw;
      line-height: 12vw;
      width: 12vw;
      height: 12vw;
      border-radius: 6vw;
      border: 0.8vw solid #fff;
    }
    .icon-color {
      right: 8vw;
      width: 12vw;
      height: 12vw;
      border-radius: 6vw;
      border: 0.8vw solid #fff;
      &.green {
        bottom: 20vw;
      }
      &.grey {
        bottom: 32vw;
      }
    }
  }

  .time {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 20vmin;
    .image {
      margin-right: 2vw;
      height: 10vmin;
      vertical-align: middle;
    }
    .text {
      font-size: 10vmin;
      line-height: 12vmin;
    }
  }
  .editor {
    margin-top: 12vmin;
    width: 40vmin;
    animation: fade 0.2s;
    .buttons {
      display: flex;
      justify-content: center;
      margin-top: 1vmin;
    }
  }
  .icon-color {
    position: fixed;
    right: 4vh;
    width: 5vh;
    height: 5vh;
    border-radius: 3vh;
    border: 0.4vh solid #fff;
    cursor: pointer;
    transition: all 0.2s;
    &:hover {
      opacity: 0.8;
    }
    &:active {
      opacity: 0.6;
    }
    &.green {
      background-color: #00b4aa;
      bottom: 10vh;
    }
    &.grey {
      background-color: #615f5f;
      bottom: 16vh;
    }
  }
  .icon-menu {
    position: fixed;
    right: 4vh;
    bottom: 4vh;
    width: 5vh;
    height: 5vh;
    border-radius: 3vh;
    border: 0.4vh solid #fff;
    font-size: 3vh;
    font-weight: bold;
    line-height: 5vh;
    cursor: pointer;
    transition: all 0.2s;
    &:hover {
      background: #fff;
      color: #00b4aa;
    }
    &:active {
      opacity: 0.6;
    }
  }
}
